// Enhanced Typography
.text-arabic-green-dark, .text-arabic-green {
  font-weight: 500;
}

// Smooth transitions for interactive elements
button, a, .hover\:bg-arabic-green\/5 {
  transition: all 0.3s ease;
}

// Enhance article readability with proper line spacing
.leading-relaxed {
  line-height: 1.8;
}

// Custom animations for decorative elements
@keyframes gentle-float {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

.gradient-animation {
  animation: gentle-float 8s ease-in-out infinite;
}

// Enhance responsiveness for smaller devices
@media (max-width: 768px) {
  .text-4xl {
    font-size: 1.75rem;
  }

  .space-y-6 {
    margin-bottom: 1.25rem;
  }
}
